import React from "react";
import styled from "styled-components";
import { useCallback, useState } from "react";

export const CreateUserInput = styled.input`
  /*  */

  /*  */
  width: 16.063rem;
  height: 3.188rem;
  border-radius: 3.125rem;

  /*  */
  text-align: center;
  font-size: 1rem;
  vertical-align: middle;

  /*  */
  background: #cbb2ff;
  border: solid #c548ff;
`;

const CreateBtn = styled.button`
  /*  */
  width: 1.813rem;
  height: 1.813rem;
  border-radius: 100%;

  /*  */
  background: #c548ff;
  border: solid 0px #c548ff;

  /*  */
  position: relative;
  top: -42.5px;
  left: 130px;

  /*  */
  &:active {
    background: #8600c6;
  }
`;

const CreateDivX = styled.div`
  /*  */
  width: 0.946rem;
  height: 0.079rem;
  border: 0px;
  border-radius: 20px;

  /*  */
  margin: 0 auto;

  /*  */
  background: #ffffff;
`;

const CreateDivY = styled(CreateDivX)`
  transform: rotate(90deg);
`;

function CreateUser({ createUser }) {
  const [name, setName] = useState("");

  const onChange = useCallback((e) => {
    setName(e.target.value);
  }, []);

  const onClick = useCallback(
    (e) => {
      if (name === "") return alert(" ");
      createUser(name);
      setName(""); // value  

      // submit    .
      //      .
      e.preventDefault();
    },
    [createUser, name]
  );

  const onKeyPress = useCallback(
    (e) => {
      if (e.key === "Enter") {
        onClick(e);
      }
    },
    [onClick]
  );
  return (
    <>
      <CreateUserInput
        placeholder="Enter the User"
        onChange={onChange}
        value={name}
        onKeyPress={onKeyPress} //  Press up..
      ></CreateUserInput>
      <CreateBtn onClick={onClick}>
        <CreateDivX />
        <CreateDivY />
      </CreateBtn>
    </>
  );
}

export default CreateUser;
